@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones{
    
    margin-top: v.$separacion ;
    @include m.tablet{
        padding: 0 40rem;
        display: flex;
        justify-content: space-between;
    }
    a{
        color:v.$negro ;
        display: block;
        margin-bottom: calc(v.$separacion /2);
        text-align: center;
    }

    
}